.dashboard__bg{
    background-color: white;
}

.dashboard__container{
    margin: 4rem 6rem;
}
.dashboard__container-h2{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 30px;
    /* identical to box height, or 136% */
    letter-spacing: -0.55px;
    
    color: #000000;
}

.dashboard__container-p{
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    /* or 167% */

    letter-spacing: -0.004em;
    margin-top: 2rem;
    margin-bottom: 2rem;
    color: #000000;

}
.dashboard__container-img{
    width: 100%;
    
}


@media screen and (max-width:550px){
    .dashboard__container{
        margin: 1rem 1rem;
    }
}